<!--  -->
<template>
  <div v-show="showDialog" id="quiz-student">
    <div class="title-bar">
      <span class="text"></span>
      <span @click="closeDialog()" class="close">X</span>
    </div>
    <div class="placeholder"></div>
  </div>
</template>

<script>
let eventEmitter = BJY.eventEmitter
let quizStudent = BJY.quizStudent

export default {
  components: {},
  data () {
    return {
      showDialog: false
    }
  },
  computed: {},
  watch: {},
  methods: {
    open (data) {
      this.showDialog = true
      this.$nextTick(() => {
        quizStudent.open({
          // 
          element: $('#quiz-student .placeholder')[0],
          replace: false,
          //  
          forceJoin: data.forceJoin,
          // id
          quizId: data.quizId,
          //  
          solution: data.solution,

          // 
          onReady: function () { },
          // 
          onError: function () { }
        })
      })
    },
    closeDialog () {
      this.showDialog = false
    }
  },
  created () {
    quizStudent.init()
    eventEmitter
      .on(
        // 
        eventEmitter.QUIZ_START,
        (e, data) => {
          console.log(data)
          this.open(data)
        }
      )
      // 
      .on(
        eventEmitter.QUIZ_END,
        (e, data) => {
          this.closeDialog()
        }
      )
      .on(
        // 
        eventEmitter.QUIZ_SOLUTION,
        (e, data) => {
          this.open(data)
        }
      )
      .on(
        eventEmitter.QUIZ_SUBMIT,
        (e, data) => {
          console.log('submit:' + data)
          this.closeDialog()
        }
      )
    eventEmitter.trigger(eventEmitter.QUIZ_REQ)
  },
  mounted () {
  },
  beforeDestroy () { }
}
</script>

