<template>
  <div id="app">
    <Loading />
    <div v-if="loaded" ref="main" id="main">
      <HorseLamp />
      <div class="class-panel">
        <WhiteBorad />
      </div>

      <div class="media-panel">
        <!--  -->
        <div class="live-teacher-player">
          <TeacherPlayer />
        </div>

        <div class="interaction-panel">
          <InteractionPanel />
        </div>
      </div>

      <div class="footer">
        <SettingPanel />
        <FunctionPanel />
      </div>

      <!--  -->
      <ModalPanel />
    </div>
  </div>
</template>

<script>
// loading
import Loading from './components/Loading.vue'
// 
import WhiteBorad from './components/WhiteBoard.vue'
// 
import SettingPanel from './components/SettingPanel'
// 
import InteractionPanel from './components/InteractionPanel'
// 
import TeacherPlayer from './components/TeacherPlayer'
// 
import FunctionPanel from './components/FunctionPanel'
// 
import ModalPanel from './components/ModalPanel'
// 
import HorseLamp from './components/HorseLamp'
// 
import language from './language/main.js'

export default {
  name: 'app',
  components: {
    Loading,
    WhiteBorad,
    SettingPanel,
    InteractionPanel,
    TeacherPlayer,
    FunctionPanel,
    HorseLamp,
    ModalPanel
  },
  data () {
    return {
      loaded: false,
      webrtc: 1
    }
  },
  mounted () {
    /**
     * flashflash
     * flash
     */
    var url = '//live-cdn.baijiayun.com/js-sdk/1.15.0/player/extension/flash.swf'
    var html = BJY.createSWF('temp', '', url)

    var swf = $(html)
    $('body').prepend(swf)
    swf.css({
      position: 'absolute',
      top: 0,
      width: 398,
      height: 298,
      opacity: 0
    })
    // flash
    if (!this.supportFlash() && !this.webrtc) {
      this.$Dialog.show({
        title: '',
        content: `Flash<a class="opt-button"
            href="http://www.adobe.com/go/getflashplayer">flash</a>`,
        confirmText: ''
      }).then(() => {
        console.log('flash')
      }).catch(e => {
        console.log(e)
      })
    }
    // webrtc 
    if (this.webrtc) {
      this.$Dialog.show({
        title: '',
        content: ``,
        confirmText: ''
      }).then(() => {
        console.log('')
      }).catch(e => {
        console.log(e)
      })
    }
  },
  created () {
    // 
    let eventEmitter = BJY.eventEmitter
    let auth = BJY.auth
    let $body = $('body')

    eventEmitter
      .one(
        eventEmitter.CLASSROOM_CONNECT_TRIGGER,
        (event, data) => {
          // 
          var extension = BJY.getExtension()
          // webrtcrealtimecat.jsundefinedRTCat
          extension.init().then(() => {
            eventEmitter.trigger(
              eventEmitter.CLASSROOM_CONNECT_TRIGGER,
              data
            )
          })
          return false
        }
      )
      .on(
        eventEmitter.CLASSROOM_CONNECT_FAIL,
        () => {
          alert(language.ROOM_SERVER_CONNECT_FAIL)
        }
      )
      .on(
        eventEmitter.LOGIN_CONFLICT,
        () => {
          alert(language.LOGIN_CONFLICT)
        }
      )
      // 
      // 
      .one(
        eventEmitter.VIEW_RENDER_TRIGGER,
        () => {
          // VIEW_RENDER_TRIGGER
          this.loaded = true

          eventEmitter.trigger(
            eventEmitter.DOC_ALL_REQ
          )

          if (auth.isStudent()) {
            $body.addClass('student')
          }

          // webRTC
          // webrtcindex.htmljs
          if (auth.isWebRTC()) {
            BJY.ScreenShareExtensionWebrtc.init()
          }
        }
      )
      .one(eventEmitter.LOADING_PANEL_END, () => {
        // ui
        $('#main').css('opacity', 1)
      })
      .on(
        eventEmitter.CLASS_START,
        () => {
          // 
          if (auth.isTeacher()) {
            eventEmitter.trigger(
              eventEmitter.MEDIA_SWITCH_TRIGGER,
              {
                audioOn: true,
                videoOn: true
              }
            )
          }
        }
      )
      .on(
        eventEmitter.CLASS_END,
        () => {
          // 
          eventEmitter.trigger(
            eventEmitter.MEDIA_SWITCH_TRIGGER,
            {
              audioOn: false,
              videoOn: false
            }
          )
        }
      )
      .on(
        eventEmitter.CLASSROOM_CONNECT_SUCCESS,
        (event, data) => {
          // data.reconnect 
        }
      )

    // 
    this.init()
  },
  methods: {
    init () {
      console.log('******************************************************************************')
      console.log('*    BJY JS SDK -  - ' + BJY.version)
      console.log('*   API https://www.baijiayun.com/js-sdk/doc/#/README')
      console.log('*   https://www.baijiayun.com/js-sdk/doc/#/change-log')
      console.log('*   https://www.baijiayun.com/js-sdk/doc/#/live?id=%e5%b8%b8%e8%a7%81%e9%97%ae%e9%a2%98')
      console.log('*   webrtcwebrtc: 0')
      console.log('*   signhttps://dev.baijiayun.com/wiki/detail/1')
      console.log('******************************************************************************')
      // demo
      var options = {
        prefixName: 'e70655665',
        env: 'production',
        room_id: '20121042083339',
        user_number: '187',
        user_avatar: 'https://img.baijiayun.com/0bjcloud/live/avatar/v2/103.jpg',
        user_name: 'student',
        user_role: 0,
        sign: '6ba34a2e6a08e4baeb4e957b4fcec31d',
        webrtc: 1
      }

      // debug 
      // webrtcurl &webrtc=0
      if (location.href.includes('debug=1')) {
        var url = prompt('')
        options = Object.assign(options, this.urlParser(url))
        console.log(options)
      }

      var classOption = {
        // 
        id: options.room_id,
        // 
        speakState: BJY.config.SPEAK_STATE_LIMIT
      }
      if (Number(options.webrtc)) {
        classOption.webrtcType = Number(options.webrtc)
      }
      this.webrtc = classOption.webrtcType

      BJY.init({
        env: options.env,
        privateDomainPrefix: options.prefixName,
        class: classOption,
        user: {
          number: options.user_number,
          avatar: decodeURIComponent(options.user_avatar),
          name: decodeURIComponent(options.user_name),
          type: Number(options.user_role)
        },
        teacher: {
          type: 1
        },
        sign: options.sign
      })
    },
    // url
    urlParser (url) {
      var params = {}
      if (url) {
        try {
          var queryStr = url.split('?')[1]
          var paramList = queryStr.split('&')
          paramList.forEach(item => {
            var pair = item.split('=')
            params[pair[0]] = pair[1]
          })
          // 
          params.env = this.getEnv(url)
          // 
          params.prefixName = url.split('.')[0].split('//')[1]
        } catch (e) {
          alert('url')
        }
      }
      return params
    },
    getEnv (url) {
      // 
      let hostName = url.match(/\/\/(.*)\./)[1]
      if (/test/.test(hostName) || /localhost/.test(hostName) || /\d{1,3}\.\d{1,3}\.\d{1,3}/.test(hostName)) {
        return 'test'
      }
      if (/beta/.test(hostName)) {
        return 'beta'
      }
      if (/www/.test(hostName)) {
        return ''
      }
      return ''
    },
    supportFlash () {
      var swf
      var plugins = navigator.plugins

      if (plugins && plugins.length > 0) {
        swf = plugins['Shockwave Flash']
      } else if (document.all) {
        // eslint-disable-next-line no-undef
        swf = new ActiveXObject('ShockwaveFlash.ShockwaveFlash')
      }
      return !!swf
    }

  }
}
</script>

